<template>
	<div class="page2_2">
		<LeftNav/>
		<div class="nav">
			<ul>
				<li v-for="item in list">
					<router-link active-class="active" class="item" :to="{path:item.page}">
					{{item.name}}
					</router-link>
				</li>
			</ul>
			<!-- <div class="fr inter">
				<interBtn title="真实审计" id="more2"/>
			</div> -->
		</div>
		<div class="content_page">
			<router-view/>
		</div>
	</div>
</template>
<script type="text/javascript">
	// import interBtn from './interactions'
	import LeftNav from './zhibiao'
	export default{
		name:'page2_2',
		components:{
			// interBtn,
			LeftNav
		},
		data(){
			return{
				list:[
				{name:'交叉验证',page:'/inside/page2_2/page2_2_1'},
				{name:'协同验证',page:'/inside/page2_2/page2_2_2'},
				// {name:'证据链追溯',page:'/inside/page2_2/page2_2_3'}
				]
			}
		},
		computed:{
			company(){
				return JSON.parse(this.$store.getters.getCompany);
			},
			userName(){
				return sessionStorage.getItem('username');
			},
		}
	}
</script>
<style type="text/css" scoped="">
	
</style>


